import { Button, Card, CardActions, CardContent, Typography } from '@material-ui/core';

import React from 'react';
import { TooltipRenderProps } from 'react-joyride';
import { CardHeader } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { steps } from '../../SheduleRow/Slot/Tour';

const useStyles = makeStyles({
  root: {
    maxWidth: '20rem'
  }
})

interface $extraProps {
  onClickNext: any,
  steps: any[],
}

export function TourTooltip(props: TooltipRenderProps & $extraProps){
  const classes = useStyles();
  return <Card {...props.tooltipProps} className={classes.root}>
    {
      !props.step.title ? null : <CardHeader title={props.step.title}/>
    }
    {
     !props.step.content ? null : <CardContent>
        {props.step.content}
      </CardContent>
    }
    {
      props.step.showSkipButton ? null :<CardActions>
        <Button onClick={props.onClickNext}>{
          props.index === (props.steps.length - 1) ? 'Done' : 'Next'
        }</Button>
      </CardActions>
    }
  </Card>
}

export default function tourToolipWrapper(extraProps: $extraProps){
  return (props: TooltipRenderProps)=>{
    return <TourTooltip {...props} {...extraProps} />
  }
}