import React, { useState, useEffect } from 'react';
import { Typography, Divider, makeStyles } from '@material-ui/core';
import classnames from 'classnames';
import { blue } from '@material-ui/core/colors';
import { useCustomSwr, SERVER_ADDRESS, get } from '../../../../utils/remote';
import SheduleRow from '../../../components/general/SheduleRow';
import ImageSlotTour, {
    dashboardSteps
} from '../../../components/general/SheduleRow/ImageSlot/Tour';
import { useSelector, useDispatch } from 'react-redux';
import {
    fullOnboardingSelector,
    createConfigAction,
    onboardingSelector
} from '../../../../utils/redux/config';
import { useHistory } from 'react-router';
import { addActivityToSchedule } from '../../../shedules/activities/added/utils';
import { take } from 'lodash';

const useStyles = makeStyles({
  header: {
    marginTop: '1rem',
    color: blue[700]
  }
})

interface $props {
  tag: string;
  ages: string[];
  portraitFeatured?: Boolean;
  activitiesLoadedCallback?: Function;
  first?: Boolean;
  imageType?: 'landscape' | 'portrait'
}

const defaultValue: any[] = [];

export default React.memo(function SearchActivitiesNonSearchRow({imageType, tag, ages, portraitFeatured = false, activitiesLoadedCallback, first}: $props){
  const classes = useStyles();
  const [ previousSizes, setPreviousSizes ] = useState({});
  const defaultTags =  ages;
  const [ onShowActivity, setOnShowActivity ] = useState<any>(null);
  const tags = defaultTags.concat([tag]).map((t: string)=>'#'+t).reverse().join(' ');
  const { data, page } = useCustomSwr(SERVER_ADDRESS+`/shedules/activities/public`, { 
    defaultValue,
    page: true,
    query: {
      tags,
      portraitFeatured,
      image_types: imageType === 'landscape' ? ['squ', 'lan'] : ['squ', 'por']
    }, 
    deps: [],
    cache: true,
    callback: activitiesLoadedCallback
  });
  const onboarding = useSelector(fullOnboardingSelector);
  const dispatch = useDispatch();
  useEffect(()=>{
    if(onboarding.dashboard.step === 1){
      setOnShowActivity(()=>()=>{
        setTimeout(()=>dispatch(createConfigAction ({
          onboarding: {
            ...onboarding,
            dashboard: {
              completed: false,
              step: onboarding.dashboard.step + 1
            }
          }
        })), 0)
      });
    }
  }, [onboarding.dashboard.step])
  useEffect(()=>{
    setPreviousSizes({
      ...previousSizes,
      [page]: data.length
    });
  }, [data]);
  return <div>
    {
      first ? <OnboardingTripwire data={data} /> : null
    }
    <Typography align="center" variant="h6" component="h3" className={classnames(classes.header, 'shedule-row-title')}>{'#'+tag}</Typography>
    {
      (!data.length || !first) ? null : <ImageSlotTour noOnEnd type="dashboard" />
    }
    {
      !data.length ? <p>No Results</p> : <SheduleRow onShowActivity={onShowActivity} imageType={imageType} first={first} data={data} />
    }
    <Divider variant="inset" />
  </div>
});

const OnboardingTripwire = React.memo(function OnboardingTripwire({data}: {data: any[]}){
  const onboarding = useSelector(fullOnboardingSelector );
  const dispatch = useDispatch ();
  const history = useHistory ();
  useEffect(()=>{
    if(data.length && onboarding?.dashboard?.step === dashboardSteps.length){
      dispatch(createConfigAction ({
        onboarding: {
          ...onboarding,
          dashboard: {
            completed: true,
            step: onboarding.dashboard.step + 1
          }
        }
      }));
      take(data, 3).forEach((activity)=>{
        dispatch(addActivityToSchedule(activity))
      });
      history.push('/activities/lineup');
    }
  }, [onboarding, data])
  return null
});