import React from 'react';
import { Card, makeStyles, CardMedia, CardContent, Typography } from '@material-ui/core';
import { useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import classNames from 'classnames';
import VisibilitySensor from 'react-visibility-sensor';
import {
    addedActivitySelector,
    wontDoActivitySelector,
    completedActivitySelector,
} from '../../../../../utils/redux/config';
import ActivitiesPreviewImages from '../../../../shedules/activities/preview/Images';
import MyPopover from '../../Popover';
import { SlotHeader, SlotTags } from '../Slot';

interface $props {
  shedule: any,
  addedActivityPage?: Boolean,
  className?: string,
  preview?: Boolean,
  firstRow?: Boolean,
  index?: number,
  imageType?: 'landscape'|'portrait',
  onShowActivity?: Function,
  showFirstActivity?: Boolean,
}
const getWidth = (props: $props)=>props.imageType === 'landscape' ? (props.shedule.image_type === 'squ' ? '175px' : '250px') : (props.shedule.image_type === 'squ' ? '250px' : '175px');

const useStyles = makeStyles((theme)=>({
  root: {
    display: "inline-block",
    width: getWidth,
    maxWidth: '100vw',
    verticalAlign: 'top',
    marginRight: '1rem',
    marginBottom: '1rem',
  },
  card: {
    width: '100%',
    height: (props: $props)=>props.imageType === 'landscape' ? '175px' : '250px',
  },
  title: {
    textDecoration: 'none',
    color: theme.palette.primary.main
  },
  img: {
    height: (props: $props)=>props.imageType === 'landscape' ? (props.shedule.imageType === 'squ' ? '175px' : '150px') : (props.shedule.imageType === 'squ' ? '225px' : '250px'),
  },
  activityTitle: {
    width: getWidth
  }
}));

const useOnHoverStyles = makeStyles({
  root: {
    width: '250px',
    maxWidth: '100vw',
    height: '500px'
  }
})



const SheduleImageSlot = React.memo(function SheduleImageSlot(props: $props){
  const [visible, setVisible] = React.useState(false);
  const activity = props.shedule;
  const addedActivity = useSelector(addedActivitySelector(activity.id));
  const wontDoActivity = useSelector(wontDoActivitySelector(activity.id));
  const completedActivity = useSelector(completedActivitySelector(activity.id));
  const classes = useStyles(props);
  const onHoverClasses = useOnHoverStyles()
  if(completedActivity || wontDoActivity) return null;
  if(!props.addedActivityPage && addedActivity) return null;
  const conditionalActivityCardClass = {
    'activity-card': (activity?.properties?.metaRequirements || []).length
  }
  const activityLinkTo = "/shedules/activities/"+activity.id;
  return <div className={classNames(props.className, classes.root)}>
      <Card key={activity.id} className={classNames(classes.card, conditionalActivityCardClass)}>
      <MyPopover
        doNotClose={props.showFirstActivity}
        showImmediately={props.showFirstActivity}
        onShowPopup={props.onShowActivity}
        ga={{
          category: 'slot',
          action: 'clickOrHover',
          label: 'previewImages'
        }}
        debounced={600}
        // popoverClasses={popoverClasses}
        popoverProps={{
          anchorOrigin: {
            vertical: 'bottom',
            horizontal: 'right',
          },
          transformOrigin: {
            vertical: 'bottom',
            horizontal: 'right',
          }
        }}
        disableHoverLeave
        renderOnHoverContent={
          ({onClose}: any)=><div onMouseLeave={onClose}>
            {/* <ImageSlotTour type="collections" conditionCheck={(onboarding: any)=>onboarding?.dashboard?.step >= 2} /> */}
          <Card className={classNames(onHoverClasses.root, conditionalActivityCardClass)}>
            <SlotHeader {...props} />
            <CardContent>
              <SlotTags tagWidth="70px" {...props} />
            </CardContent>
            <Link to={props.preview ? '/overview' : activityLinkTo}>
              <ActivitiesPreviewImages activity={activity} />
            </Link>

          </Card>
          </div>
        }
        renderHoverableContent={
          ()=><div className={classNames(
            // classes.imageTag, 
            'image-tag')} style={{
            cursor: activity.properties.imgLinks.length < 2 ? 'default' : 'pointer'
          }}>
            <VisibilitySensor partialVisibility onChange={(nextVisible)=>setVisible(visible || nextVisible)}>
              {
                visible ? <CardMedia title={activity.name} className={classes.img} image={activity.image}/> :
                <div className={classes.img} />
              }
              
            </VisibilitySensor>
            
        </div>
        }
      />
      
    </Card>
    <div className={classes.activityTitle}>
      <Link to={`/shedules/activities/${activity.id}`}>
        <Typography align="center" variant="body2">
          {
            activity.name
          }
        </Typography>
      </Link>
    </div>
  </div>
});


export default SheduleImageSlot