import React from 'react';
import Tour from '../../../Tour';
import { useDispatch, useSelector } from 'react-redux';
import {
    createConfigAction,
    fullOnboardingSelector
} from '../../../../../../utils/redux/config';
import { Typography } from '@material-ui/core';


export default function ImageSlotTour(props: {noOnEnd?: Boolean, type: 'dashboard'|'collections'|'lineup', conditionCheck?: Function }){
  const dispatch = useDispatch ();
  const onboarding = useSelector(fullOnboardingSelector );
  const {step, completed} = onboarding[props.type] || {step: 0, completed: false};
  const steps = allSteps[props.type];
  const finished = completed || step >= steps.length;
  if(finished || (props.conditionCheck ? !props.conditionCheck(onboarding) : finished)) return null;
  return <>
    {
      (step >= steps.length || completed) ? null : <Tour
      step={step}
      joyrideProps={
        {
          continuous: true,
          run: true,
          scrollToFirstStep: true,
          showProgress: true,
          showSkipButton: false,
          // disableOverlayClose: true
          // disableCloseOnEsc: true,
          styles: {
            options: {
              zIndex: 10000,
            }
          },
          steps,
        }
      }
      onChange={(nextStep: number)=>{
        dispatch(createConfigAction({
          onboarding: {
            ...onboarding,
            [props.type]: {
              completed: false,
              step: nextStep,
            }
          }
        }))
      }}
      onEnd={props.noOnEnd ? undefined : (nextStep: number)=>{
        dispatch(
          createConfigAction({
            onboarding: {
              ...onboarding,
              [props.type]: {
                completed: true,
                step: nextStep,
              }
            },
          }))
        }
      }
      />
    }
  </>
}
