import React from 'react';
import { makeStyles } from '@material-ui/core';
import Slideshow from '../../../../components/general/Slideshow';

const noop = ()=>{}
const useStyles = makeStyles(theme => ({
  img: {
    width: '100%',
    height: '19rem',
    backgroundSize: 'cover !important',
    position: 'relative'
    // overflowY: 'hidden',
  },
  close: {
    position: 'absolute',
    zIndex: 2,
    right: 0,
    top: 0,
    cursor: 'pointer',
    fontSize: '1.5rem',
    padding: '0.5rem',
  }
}));

export default function ActivitiesPreviewImages({onClick, activity, className, onClose}: {onClick?: any, onClose?: any, activity: any, className?: string}){
  const classes = useStyles();
  return <span onClick={onClick} onMouseLeave={onClose || noop}>
      <Slideshow images={(!activity.properties.imgLinks.length ? [{src: activity.image}] : activity.properties.imgLinks).map((img: any) => <div key={img.src} className={classes.img} style={{'background': `url(${img.src}) no-repeat center center`}}>
      {
        !onClose ? null : <span onClick={onClose} className={classes.close}>x</span>
      }
    </div>)}/>
  </span>
}

