import React, { useEffect, useState } from 'react';
import { Grid, Typography } from '@material-ui/core';
import { Link, useHistory } from 'react-router-dom';

import { useCustomSwr, SERVER_ADDRESS } from '../../../../../utils/remote';
import SheduleRow from '../../../../components/general/SheduleRow';
import { useSelector, useDispatch } from 'react-redux';

import DayTimeline from '../Timeline';
import { updateActivities, purgeActivities } from '../utils';
import Accordions from '../../../../materials/Accordions';
import {
    addedActivitiesSelector,
    fullOnboardingSelector,
    createConfigAction
} from '../../../../../utils/redux/config';
import { uiSelector, createUIAction } from '../../../../../utils/redux/ui';
import { useMediaQuery } from '@material-ui/core';
import MyTabs, { TabPanel } from '../../../../components/general/Tabs';
import { MoreVert } from '@material-ui/icons';

import ImageSlotTour, {
    lineupSteps
} from '../../../../components/general/SheduleRow/ImageSlot/Tour';






export default function ActivityLineup(props: any){
  
  const addedActivities = useSelector(addedActivitiesSelector);
  const dispatch = useDispatch();
  const ui = useSelector(uiSelector);
  const [tab, setTab] = useState(ui.addedActivities.tab || 0);
  const onboarding = useSelector(fullOnboardingSelector);
  const defaultValue : any = []
  const history = useHistory();
  const { data } = useCustomSwr(SERVER_ADDRESS+`/shedules/activities/public`, { 
    defaultValue,
    query: {
      ids: Object.keys(addedActivities)
    },
    doNotFire:  !Object.keys(addedActivities).length,
    deps: [],
  });
  React.useEffect(()=>{
    if(data === defaultValue) return;
    if(data.length !== Object.keys(addedActivities).length){
      purgeActivities(data)
    }
  }, [data])

  useEffect(()=>{
    if(onboarding?.lineup?.step === lineupSteps.length){
      dispatch(createConfigAction({
        onboarding: {
          ...onboarding,
          lineup: {
            completed: true,
            step: onboarding.lineup.step + 1
          }
        }
      }));
      history.push('/activities/added');
    }
  }, [onboarding, data])

  const biggerThanSmall = useMediaQuery('(min-width:1000px)');
  const noActivitiesText = <p>No activities in lineup. Add activities by clicking the <MoreVert /> icon in the activity cards</p>
  const inScheduleActivities = data.filter((activity: any)=>addedActivities[activity.id]?.inSchedule);
  const schedule = !inScheduleActivities.length ? noActivitiesText : <DayTimeline mutate={updateActivities} data={inScheduleActivities.map((activity: any) => ({
    id: activity.id,
    activity,
    ordinal: addedActivities[activity.id].ordinal
  }))} />
  const materials = !inScheduleActivities.length ? noActivitiesText : <Accordions data={[...inScheduleActivities].sort((a,b)=>addedActivities[a.id].ordinal-addedActivities[b.id].ordinal)} />
  
  return biggerThanSmall ? <div>
    <ImageSlotTour noOnEnd type="lineup" />
    <Grid container>
      {
        !inScheduleActivities.length ? <Grid xs={12}>
          <Typography>
          {
            noActivitiesText
          }
          </Typography>
        </Grid> :
        <Grid xs={12}>
          <Typography align="center" variant="h6">
            Activity Lineup
          </Typography>
          <Grid container>
            <Grid xs={6} item>
              {
                schedule
              }
            </Grid>
            <Grid xs={6}item>
              {
                materials
              }
            </Grid>
          </Grid>
        </Grid>
      }
    </Grid>
  </div> : <div>
  <ImageSlotTour noOnEnd type="lineup" />
      <div>
        <MyTabs
          tabsProps={{variant: "standard"}}
          value={tab}
          onChange={(newValue: number)=>{
            setTab(newValue)
            dispatch(createUIAction({
              addedActivities: {
                tab: newValue
              }
            }))
          }}
          tabPropsArr={
            [
              {
                label: "Lineup"
              },
              {
                label: "Materials"
              }
            ]
          } />
      </div>
      <TabPanel value={tab} index={0}>
        {
          schedule
        }
      </TabPanel>
      <TabPanel value={tab} index={1}>
        <Grid container>
          <Grid item sm={12} md={6}>
            {
              materials
            }
          </Grid>
        </Grid>
      </TabPanel>
    </div>
}