import React from 'react';
import { Popover } from '@material-ui/core';
import { debounce, uniqueId } from 'lodash';
import { $sendEventProps, sendEvent } from '../../../../utils/ga';

type $props = {
  disableHover?: Boolean,
  disablePopup?: Boolean,
  className?: string,
  renderHoverableContent: Function,
  renderOnHoverContent: Function,
  popoverProps?: any,
  popoverClasses?: Object,
  ga?: $sendEventProps,
  debounced?: number,
  disableHoverLeave?: Boolean,
  onShowPopup?: Function,
  showImmediately?: Boolean,
  doNotClose?: Boolean,
}

export default React.memo(function MyPopover({doNotClose, showImmediately, onShowPopup, disableHoverLeave, ga, disablePopup, disableHover, className, popoverClasses, renderHoverableContent, renderOnHoverContent, popoverProps={}, debounced=500} : $props){
  const [mouseStopped, setMouseStopped] = React.useState<string|null>(null);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [candidateAnchorEl, setCandidateAnchorEl] = React.useState(null);
  const hoverableEl = React.useRef(null);
  const [doNotHide, setDoNotHide] = React.useState(false);
  const debouncedSetMouseStopped = debounce((id: string)=>setMouseStopped(id), debounced);
  const [currentId, setCurrentId] = React.useState<string|null>(null);
  const showPopup = (el: any)=>{
    setAnchorEl(el)
    if(onShowPopup){
      setDoNotHide(true);
      onShowPopup(()=>{
        setDoNotHide(false);
        handleHide(true);
      })
    }
  }
  React.useEffect(()=>{
    if(showImmediately && hoverableEl?.current){
      setAnchorEl(hoverableEl.current)
    }
  }, [hoverableEl])
  React.useEffect(()=>{
    if(candidateAnchorEl && mouseStopped && mouseStopped === currentId){
      if(ga){
        sendEvent({
          ...ga,
          action: ga.action+':hover'
        });
      }
      showPopup(candidateAnchorEl);
    }
  }, [candidateAnchorEl, mouseStopped])
  
  const handleHide = (override = false)=>{
    if((!doNotHide || override) && !doNotClose){
      setCandidateAnchorEl(null);
      setAnchorEl(null);
      setMouseStopped(null)
    }
  }
  const hoverableContentProps = {
    onClick(event: any){
      if(ga){
        sendEvent(ga);
      }
      showPopup(event.currentTarget)
    },
    onMouseOver(event: any){
      if(!disableHover && !onShowPopup && event.currentTarget !== candidateAnchorEl) {
        const nextCurrentId = uniqueId()
        setCurrentId(nextCurrentId)
        setCandidateAnchorEl(event.currentTarget)
        debouncedSetMouseStopped(nextCurrentId)
      }
    },
    onMouseLeave(event: any){
      if((!disableHover && !disableHoverLeave) || !anchorEl) {
        setCandidateAnchorEl(null)
      }
    },
  }
  const onClose = ()=>{
    handleHide()
    if(popoverProps.onClose) popoverProps.onClose();
  }
  if(disablePopup)
    return <div className={className}>{renderHoverableContent()}</div>
  return <><div ref={hoverableEl} className={className}
        {...hoverableContentProps}
    >
      {
        renderHoverableContent()
      }
    </div>
    <Popover
      classes={popoverClasses}
      open={Boolean(anchorEl)}
      anchorEl={anchorEl}
      {...popoverProps}
      onClose={onClose}
      disableRestoreFocus
    >
      <div>
        {
          renderOnHoverContent({onClose})
        }
      </div>
    </Popover>
  </>
});