import React, { useEffect, useState } from 'react';
import _ from 'lodash';
import LazyLoad from 'react-lazyload';

import SearchActivitiesNonSearchRow from '../NonSearchRow';
import SearchActivitiesNonSearchPortraitRow from '../NonSearchPortraitRow';
import SearchActivitiesNonSearchSquareRow from '../NonSearchSquareRow';
import lscache from 'lscache';
import { useCustomSwr, SERVER_ADDRESS } from '../../../../utils/remote';

const LIST_ACTIVITIES_TAG_ORDER = 'listActivitiesTagOrder';

export default React.memo(function ListActivities({ages}: {subjects: string[], ages: string[]}){
  const [tagOrder, setTagOrder] = useState<string[]>(JSON.parse(lscache.get(LIST_ACTIVITIES_TAG_ORDER) || '[]'));
  const { data: popularTags } : {data: string[]} = useCustomSwr(SERVER_ADDRESS+'/tags/popular', {
    cache: true,
    defaultValue: []
  });
  useEffect(()=>{
    if(!tagOrder.length && popularTags.length){
      const nextTagOrder = _.uniq((_.shuffle([/*'active',*/ 'stem', 'arts&crafts', 'indoor', 'outdoor', 'literacy', 'math', 'printable', 'winter'])).concat(_.shuffle(popularTags||[]).filter((tag: string)=>(tag.indexOf('age') === -1))))
      setTagOrder(nextTagOrder);
      lscache.set(LIST_ACTIVITIES_TAG_ORDER, JSON.stringify(nextTagOrder), 15)
    }
  }, [ages, popularTags])

  const squareRender = (tag: string)=>{
    return <div key={tag} style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} >
      <SearchActivitiesNonSearchSquareRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }

  const portraitRender = (tag: string)=>{
    return <div key={tag} style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={200} >
      <SearchActivitiesNonSearchPortraitRow tag={tag} ages={ages} />
    </LazyLoad></div>;
  }
  
  const render = (tag: string, index: number)=>{
    return <div key={tag} style={{minHeight: '300px', minWidth: '100%'}}><LazyLoad height={300}>
      <SearchActivitiesNonSearchRow imageType={(index % 2 === 1) ? 'landscape' : 'portrait'} first={index === 0} tag={tag} ages={ages} />
    </LazyLoad></div>;
  }
  return <div>
    {
      // [tagOrder[0]]
      tagOrder
      .map((tag: string, i: number)=>{
        if(!tag) return null;
        if(i === 1 || i === 8) return squareRender(tag);
        if(i === 3 || i === 6 || i === 12) return portraitRender(tag)
        return render(tag, i)
      })
    }
  </div>
});