import React from 'react';
import ReactJoyride from 'react-joyride';
import tourTooltipWrapper from './Tooltip';

interface $props {
  onEnd?: Function;
  onChange?: Function;
  joyrideProps: any;
  step?: number;
}

export default function Tour({onChange, onEnd, joyrideProps, step}: $props){
  

  const onClickNext = ()=>{
    const nextStepIndex = (step || 0) + 1;
    if(nextStepIndex === joyrideProps.steps.length && onEnd){
      onEnd(nextStepIndex);
    } else {
      onChange && onChange(nextStepIndex);
    }
  }

  return <ReactJoyride
    {...joyrideProps}
    stepIndex={step}
    // callback={handleCallback}
    tooltipComponent={tourTooltipWrapper({onClickNext, steps: joyrideProps.steps})}
  />
}